import React from "react";
import { makeStyles } from "@material-ui/styles";
import {
  Box,
  Grid,
  Card,
  CardActionArea,
  CardContent,
  CardActions,
  CardMedia,
  Button,
  Typography,
} from "@material-ui/core";
import Navbar from "./Navbar";
import TechReach from "../images/TechReach.jpg";
// import CGPA from "../images/CGPA.jpg";
// import MyPortfolio from "../images/MyPortfolio.jpg";
import FirebaseLogin from "../images/FirebaseLogin.jpg";
import ProjectN from "../images/ProjectN.png";

const myStyles = makeStyles({
  mainContainer: {
    backgroundColor: "white",
    // background: 'skyBlue',
    height: "100%",
    // position: 'rlative',
  },
  cardContainer: {
    maxWidth: "345",
    height: '80%',
    // margin: '3rem',
    margin: "1rem 2.5rem 5rem 2.5rem",
    backgroundColor: "skyBlue",
    color: "navy",
    textAlign: "justify",
    textAlign: "center",
  },
});

 const Portfolio = ({portfolioProjects}) => {
  const classes = myStyles();
  return (

    <Box component="div" className={classes.mainContainer}>
      <Navbar />
      
      <Grid container justify="center">
    
        {
          portfolioProjects && portfolioProjects.map(({liveLink, techUsed, projectName, projectImage, workInProgress}, index) => 
                 
                
        <Grid item xs={12} sm={6} md={4}>
        <Card className={classes.cardContainer} key={index}>
          <CardActionArea>
            <CardMedia component="img" height="180" image={projectImage} />

            <CardContent>
              <Typography gutterBottom variant="h4">
                {projectName}
              </Typography>

              <Typography
                style={{ color: "navy", textAlign: "justify" }}
                variant="body"
                color="textSecondary"
                component="p"
              >
                <br /> <br />
                {techUsed}
              </Typography>
            </CardContent>
          </CardActionArea>
          <CardActions>
            {/* <Button size='large' color='navy'>
                                  Share
                              </Button> */}
            <a
              style={{ textDecorationLine: "none" }}
              href={liveLink}
              target="_blank"
            >
              {" "}
              <Button size="large" color="navy">
                VIEW {workInProgress}
              </Button>
            </a>
          </CardActions>
        </Card>
        </Grid>
          )
      }
          
       

        
      
      </Grid>
    </Box>
  );
}
export default Portfolio;
